<script>
export default {
  name: 'AppConfig',
  props: {
    config: {
      type: Object,
      default: () => ({})
    },
    cmdlineConfig: {
      type: Object,
      default: () => ({})
    },
    loadingStates: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['refresh-config'],
  data() {
    return {
      activeTab: 'cmdline'
    };
  },
  methods: {
    formatKey(key) {
      // Â∞ÜÈ©ºÂ≥∞ÂëΩÂêçËΩ¨Êç¢‰∏∫ÂèØËØªÊ†ºÂºè
      return key;
    },
    formatValue(value) {
      if (value === null) return 'null';
      if (value === undefined) return 'undefined';
      if (typeof value === 'object') {
        return JSON.stringify(value, null, 2);
      }
      return String(value);
    }
  }
}
