      <!-- ‰ªªÂä°Ë°®Ê†º -->
      <div v-if="paginatedTasks.length > 0" class="table-container">
        <table class="task-table">
          <thead>
            <tr>
              <th @click="sortBy('name')" class="sortable">
                ‰ªªÂä°Âêç <span class="sort-indicator" v-if="sortField === 'name'">{{ sortOrder === 'asc' ? '‚Üë' : '‚Üì' }}</span>
              </th>
              <th>ÈõÜÁæ§</th>
              <th>Ë°®Âêç</th>
              <th>topic</th>
              <th>Ê∂àË¥πËÄÖÁªÑ</th>
              <th @click="sortBy('type')" class="sortable">
                Á±ªÂûã <span class="sort-indicator" v-if="sortField === 'type'">{{ sortOrder === 'asc' ? '‚Üë' : '‚Üì' }}</span>
              </th>
              <th @click="sortBy('status')" class="sortable">
                Áä∂ÊÄÅ <span class="sort-indicator" v-if="sortField === 'status'"> {{ sortOrder === 'asc' ? '‚Üë' : '‚Üì' }}</span>
              </th>
              <th @click="sortBy('lag')" class="sortable">
                kafka Lag <span class="sort-indicator" v-if="sortField === 'lag'"> {{ sortOrder === 'asc' ? '‚Üë' : '‚Üì' }}</span>
              </th>
              <th @click="sortBy('writeSpeed')" class="sortable">
                ÂÜôÂÖ•ÈÄüÂ∫¶ <span class="sort-indicator" v-if="sortField === 'writeSpeed'">{{ sortOrder === 'asc' ? '‚Üë' : '‚Üì' }}</span>
              </th>
              <th>Êìç‰Ωú</th>
