          </thead>
          <tbody>
            <tr v-for="task in paginatedTasks" :key="task.Name">
              <td><span class="task-name">{{ task.Name }}</span></td>
              <td>{{ task.Cluster || 'N/A' }}</td>
              <td>{{ task.Table || 'N/A' }}</td>
              <td>{{ task.Topic || 'N/A' }}</td>
              <td>{{ task.ConsumerGroup || 'N/A' }}</td>
              <td>
                <span :class="['task-type', task.Type]">{{ task.Type === 'metric' ? 'ÊåáÊ†á' : 'Êó•Âøó' }}</span>
              </td>
              <td>
                <span :class="['status-badge', `status-${task.Status.toLowerCase()}`]">
                  {{ task.Status }}
                </span>
              </td>
              <td :class="{ 'lag-critical': task.Lag > 10000, 'lag-warning': task.Lag > 1000 && task.Lag <= 10000 }">
                {{ task.Lag || 0 }}
              </td>
              <td>{{ task.Rate || 'N/A' }}</td>
              <td>
                <button class="btn-small" @click="viewTaskConfig(task.Name)">ÈÖçÁΩÆ</button>
                <button class="btn-small" @click="viewTaskDbKey(task.Name)">dbkey</button>
                <!-- Ê†πÊçÆStatusÂÄºÊòæÁ§∫‰∏çÂêåÁöÑÊìç‰ΩúÊåâÈíÆ -->
                <button class="btn-small" :disabled="loadingStates.tasks" v-if="task.Status === 'Dead'">ÈáçÂêØ</button>
              </td>
            </tr>
          </tbody>
        </table>

        <!-- ÂàÜÈ°µ -->
        <div class="pagination">
          <button class="btn-small" :disabled="currentPage === 1" @click="prevPage">‰∏ä‰∏ÄÈ°µ</button>
          <span class="page-info">{{ currentPage }} / {{ totalPages }}</span>
          <button class="btn-small" :disabled="currentPage === totalPages" @click="nextPage">‰∏ã‰∏ÄÈ°µ</button>
        </div>
      </div>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <div v-else class="empty-state">
        ÊöÇÊó†‰ªªÂä°Êï∞ÊçÆ
      </div>
    </div>

    <!-- ‰ªªÂä°ËØ¶ÊÉÖÂºπÁ™ó -->
    <!-- ÈÖçÁΩÆÂºπÁ™ó -->
    <div v-if="showConfigModal" class="modal-overlay" @click.self="closeConfigModal">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title">‰ªªÂä°ÈÖçÁΩÆ - {{ currentTaskName }}</h3>
          <button class="modal-close" @click="closeConfigModal">&times;</button>
        </div>
        <div class="modal-body">
          <div v-if="loadingConfig" class="loading">Âä†ËΩΩ‰∏≠...</div>
          <div v-else class="json-display-container">
            <pre class="json-display" v-html="highlightJson(taskConfig)"></pre>
          </div>
        </div>
      </div>
    </div>

    <!-- dbkeyÂºπÁ™ó -->
    <div v-if="showDbKeyModal" class="modal-overlay" @click.self="closeDbKeyModal">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title">‰ªªÂä°dbkey - {{ currentTaskName }}</h3>
          <button class="modal-close" @click="closeDbKeyModal">&times;</button>
        </div>
        <div class="modal-body">
          <div v-if="loadingDbKey" class="loading">Âä†ËΩΩ‰∏≠...</div>
          <div v-else>
            <!-- Ê∑ªÂä†ÊêúÁ¥¢Á≠õÈÄâÂäüËÉΩ -->
            <div class="search-container" style="margin-bottom: 16px;">
              <input 
                type="text" 
                v-model="dbKeySearchKeyword"
                placeholder="ÊêúÁ¥¢Êï∞ÊçÆÂ∫ìÂêç..."
                style="width: 250px; padding: 6px 12px; border: 1px solid #ddd; border-radius: 4px; font-size: 14px;"
              >
              <span style="margin-left: 8px; color: #666; font-size: 14px;">
                ÂÖ± {{ Array.isArray(taskDbKey) ? taskDbKey.length : 0 }} Êù°ËÆ∞ÂΩï
                <template v-if="dbKeySearchKeyword">, Á≠õÈÄâÂêé {{ filteredDbKeyData.length }} Êù°</template>
              </span>
            </div>
            
            <!-- Áõ¥Êé•Ê∏≤ÊüìË°®Ê†ºÔºå‰∏ç‰ΩøÁî®Â§çÊùÇÁöÑÊù°‰ª∂Âà§Êñ≠ -->
            <table class="dbkey-table">
              <thead>
                <tr>
                  <th>Êï∞ÊçÆÂ∫ìÂêç</th>
                  <th>insertSQL</th>
                  <th>seriesMapË°®SQL</th>
                  <th>Â§ÑÁêÜÊï∞ÊçÆ</th>
                  <th style="min-width: 100px; width: 100px;">Â≠óÊÆµ‰∏™Êï∞</th>
                  <th>series‰∏ãÊ†á</th>
                </tr>
              </thead>
              <tbody>
                <!-- Á°Æ‰øùtaskDbKeyÊòØÊï∞ÁªÑÂπ∂Â∫îÁî®Á≠õÈÄâ -->
                <tr v-for="(item, index) in filteredDbKeyData" :key="index">
                  <td>{{ item.name || '-' }}</td>
                  <td 
                    class="sql-cell" 
                    :title="item.prepareSQL"
                    @dblclick="copyToClipboard(item.prepareSQL)"
                  >{{ item.prepareSQL || '-' }}</td>
                  <td 
                    class="sql-cell" 
                    :title="item.promSerSQL"
                    @dblclick="copyToClipboard(item.promSerSQL)"
                  >{{ item.promSerSQL || '-' }}</td>
                  <td>{{ item.processed || '-' }}</td>
                  <td>{{ item.numDims || '-' }}</td>
                  <td>{{ item.idxSerId || '-' }}</td>
                </tr>
                <!-- Â¶ÇÊûúÊ≤°ÊúâÊï∞ÊçÆÔºåÊòæÁ§∫ÊèêÁ§∫Ë°å -->
                <tr v-if="filteredDbKeyData.length === 0">
                  <td colspan="6" style="text-align: center; color: #666; padding: 20px;">
                    {{ dbKeySearchKeyword ? 'Ê≤°ÊúâÊâæÂà∞ÂåπÈÖçÁöÑÊï∞ÊçÆÂ∫ìÂêç' : 'ÊöÇÊó†Êï∞ÊçÆ' }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../services/api';

export default {
  name: 'TasksList',
  props: {
    tasks: {
      type: Array,
      default: () => []
    },
    loadingStates: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['refresh-tasks'],
  data() {
    return {
      taskFilter: {
        name: '',
        type: '',
        status: ''
      },
      sortField: 'name',
      sortOrder: 'asc',
      currentPage: 1,
      pageSize: 10,
      selectedTask: null,
      showConfigModal: false,
      showDbKeyModal: false,
      currentTaskName: '',
      taskConfig: null,
      taskDbKey: null,
      loadingConfig: false,
      loadingDbKey: false,
      dbKeySearchKeyword: ''
    };
  },
  computed: {
    // Á≠õÈÄâdbkeyÊï∞ÊçÆ
    filteredDbKeyData() {
      if (!Array.isArray(this.taskDbKey)) {
        return [];
      }
      
      const keyword = this.dbKeySearchKeyword.toLowerCase().trim();
      if (!keyword) {
        return this.taskDbKey;
      }
      
      return this.taskDbKey.filter(item => {
        return item.name && item.name.toLowerCase().includes(keyword);
      });
    },
      filteredTasks() {
      // Á°Æ‰øùtasksÊòØÊï∞ÁªÑÁ±ªÂûã
      let result = Array.isArray(this.tasks) ? [...this.tasks] : [];
      
      // Â∫îÁî®ËøáÊª§
      if (this.taskFilter.name) {
        result = result.filter(task => 
          task && task.Name && task.Name.toLowerCase().includes(this.taskFilter.name.toLowerCase())
        );
      }
      if (this.taskFilter.type) {
        result = result.filter(task => task && task.Type === this.taskFilter.type);
      }
      if (this.taskFilter.status) {
        result = result.filter(task => task && task.Status && task.Status.toLowerCase() === this.taskFilter.status.toLowerCase());
      }
      
      // Â∫îÁî®ÊéíÂ∫è
      result.sort((a, b) => {
        // Á°Æ‰øùÊØîËæÉÁöÑÂ±ûÊÄßÂ≠òÂú®
        if (!a || !b) return 0;
        
        // Êò†Â∞ÑÊéíÂ∫èÂ≠óÊÆµÂà∞APIËøîÂõûÁöÑÂ≠óÊÆµÂêç
        const fieldMap = {
          'name': 'Name',
          'type': 'Type',
          'status': 'Status',
          'lag': 'Lag',
          'writeSpeed': 'Rate'
        };
        
        const actualField = fieldMap[this.sortField] || this.sortField;
        let aVal = a[actualField];
        let bVal = b[actualField];
        
        if (typeof aVal === 'string' && typeof bVal === 'string') {
          aVal = aVal.toLowerCase();
          bVal = bVal.toLowerCase();
        }
        
        if (aVal < bVal) return this.sortOrder === 'asc' ? -1 : 1;
        if (aVal > bVal) return this.sortOrder === 'asc' ? 1 : -1;
        return 0;
      });
      
      return result;
    },
    formattedTaskData() {
      if (!this.selectedTask) return '';
      return JSON.stringify(this.selectedTask, null, 2);
    },
    paginatedTasks() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredTasks.slice(start, end);
    },
    totalPages() {
      return Math.ceil(this.filteredTasks.length / this.pageSize);
    }
  },
  methods: {
    applyFilters() {
      this.currentPage = 1;
    },
    sortBy(field) {
      if (this.sortField === field) {
        this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
      } else {
        this.sortField = field;
        this.sortOrder = 'asc';
      }
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    handlePageSizeChange() {
      this.currentPage = 1;
    },
    refreshTasks() {
      this.$emit('refresh-tasks');
    },
    async viewTaskConfig(taskName) {
      this.currentTaskName = taskName;
      this.loadingConfig = true;
      try {
        const response = await api.getTaskConfig(taskName);
        this.taskConfig = JSON.stringify(response.data, null, 2);
        this.showConfigModal = true;
      } catch (error) {
        console.error('Ëé∑Âèñ‰ªªÂä°ÈÖçÁΩÆÂ§±Ë¥•:', error);
        alert('Ëé∑Âèñ‰ªªÂä°ÈÖçÁΩÆÂ§±Ë¥•');
      } finally {
        this.loadingConfig = false;
      }
    },
    async viewTaskDbKey(taskName) {
      this.currentTaskName = taskName;
      this.loadingDbKey = true;
      try {
        const response = await api.getTaskDbKey(taskName);
        
        // ÊòéÁ°ÆÂ§ÑÁêÜentityÂ≠óÊÆµ‰∏≠ÁöÑÊï∞ÊçÆ
        if (response.data && response.data.entity) {
          // Á°Æ‰øùÊòØÊï∞ÁªÑÊ†ºÂºè
          const rawData = Array.isArray(response.data.entity) ? response.data.entity : [response.data.entity];
          
          // ËΩ¨Êç¢Êï∞ÊçÆÂ≠óÊÆµÂêçÔºöÂ∞ÜÂ§ßÂÜôÂºÄÂ§¥ÁöÑÂ≠óÊÆµÊò†Â∞Ñ‰∏∫Â∞èÂÜôÂºÄÂ§¥ÁöÑÂ≠óÊÆµÔºåÁ°Æ‰øù‰∏éË°®Ê†ºÁªëÂÆöÂåπÈÖç
          this.taskDbKey = rawData.map(item => ({
            name: item.Name || '-',
            prepareSQL: item.PrepareSQL || '-',
            promSerSQL: item.PromSerSQL || '-',
            processed: item.Processed || '-',
            numDims: item.NumDims || '-',
            idxSerId: item.IdxSerID || '-'
          }));
        } else {
          // ÈªòËÆ§ÊÉÖÂÜµ: Á©∫Êï∞ÁªÑ
          this.taskDbKey = [];
        }
        
        // Âº∫Âà∂ÊòæÁ§∫ÂºπÁ™óÔºåÁ°Æ‰øùÊï∞ÊçÆÂä†ËΩΩÂêéÂºπÁ™óÂèØËßÅ
        this.showDbKeyModal = true;
      } catch (error) {
        console.error('Ëé∑Âèñ‰ªªÂä°dbkeyÂ§±Ë¥•:', error);
        alert('Ëé∑Âèñ‰ªªÂä°dbkeyÂ§±Ë¥•');
      } finally {
        this.loadingDbKey = false;
      }
    },
    highlightJson(jsonStr) {
      if (!jsonStr) return '';
      
      // ‰ΩøÁî®Êõ¥ÂèØÈù†ÁöÑJSONÈ´ò‰∫ÆÊñπÊ≥ïÔºåÈÅøÂÖçÈáçÂ§çÊõøÊç¢
      try {
        // È¶ñÂÖàÊõøÊç¢Â∏ÉÂ∞îÂÄºÂíånull
        let highlighted = jsonStr
          .replace(/\b(true|false)\b/g, '<span class="boolean">$1</span>')
          .replace(/\b(null)\b/g, '<span class="null">$1</span>')
          .replace(/\b(\d+(?:\.\d+)?)\b/g, '<span class="number">$1</span>');
        
        // ‰ΩøÁî®Êõ¥Á≤æÁ°ÆÁöÑÊñπÂºèÂ§ÑÁêÜÈîÆÂíåÂ≠óÁ¨¶‰∏≤ÂÄºÔºåÈÅøÂÖçÈáçÂ§çÊõøÊç¢
        // ÂÖàÂåπÈÖçÈîÆÂÄºÂØπ‰∏≠ÁöÑÈîÆ
        highlighted = highlighted.replace(/("[^"\\]*(?:\\.[^"\\]*)*")\s*:/g, '<span class="key">$1</span>:');
        // ÁÑ∂ÂêéÂåπÈÖçÂâ©‰ΩôÁöÑÂ≠óÁ¨¶‰∏≤ÂÄºÔºà‰∏çÂåÖÊã¨Â∑≤ÁªèË¢´Â§ÑÁêÜËøáÁöÑÈîÆÔºâ
        highlighted = highlighted.replace(/(?::\s*)("[^"\\]*(?:\\.[^"\\]*)*")/g, ': <span class="string">$1</span>');
        
        return highlighted;
      } catch (e) {
        console.error('JSONÈ´ò‰∫ÆÂ§±Ë¥•:', e);
        return jsonStr; // Â¶ÇÊûúÂ§ÑÁêÜÂ§±Ë¥•ÔºåËøîÂõûÂéüÂßãÂ≠óÁ¨¶‰∏≤
      }
    },
    
    // Â§çÂà∂ÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
    copyToClipboard(text) {
      if (!text) return;
      
      // ÂàõÂª∫‰∏¥Êó∂ÂÖÉÁ¥†
      const textarea = document.createElement('textarea');
      textarea.value = text;
      textarea.style.position = 'fixed';
      textarea.style.opacity = '0';
      document.body.appendChild(textarea);
      
      // ÈÄâÊã©Âπ∂Â§çÂà∂
      textarea.select();
      document.execCommand('copy');
      
      // Ê∏ÖÁêÜ
      document.body.removeChild(textarea);
      
      // ÊòæÁ§∫Â§çÂà∂ÊàêÂäüÊèêÁ§∫
      const originalTitle = document.title;
      document.title = 'Â§çÂà∂ÊàêÂäüÔºÅ';
      setTimeout(() => {
        document.title = originalTitle;
      }, 1000);
    },
    closeConfigModal() {
      this.showConfigModal = false;
      this.taskConfig = null;
    },
    closeDbKeyModal() {
      this.showDbKeyModal = false;
      this.taskDbKey = null;
    },
    startTask(task) {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®APIÂêØÂä®‰ªªÂä°
      console.log('ÂêØÂä®‰ªªÂä°:', task.name);
      this.refreshTasks();
    },
    stopTask(task) {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®APIÂÅúÊ≠¢‰ªªÂä°
      console.log('ÂÅúÊ≠¢‰ªªÂä°:', task.name);
      this.refreshTasks();
    },
    restartTask(task) {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®APIÈáçÂêØ‰ªªÂä°
      console.log('ÈáçÂêØ‰ªªÂä°:', task.name);
      this.refreshTasks();
    }
  }
};
</script>

