// src/services/api.js
import axios from 'axios'

const api = axios.create({})

export default {
  // Ëé∑ÂèñÁ≥ªÁªüÊ¶ÇËßà‰ø°ÊÅØ
  getOverview() {
    return api.get('/api/v1/metrics/procinfo')
  },
  
  // Ëé∑Âèñ‰ªªÂä°ÂàóË°®
  getTasks() {
    return api.get('/api/v1/tasks')
  },
  
  // Ëé∑ÂèñÈÖçÁΩÆ‰ø°ÊÅØ
  getConfig() {
    return api.get('/api/v1/config')
  },
  
  // Ëé∑ÂèñÂëΩ‰ª§Ë°åÂèÇÊï∞
  getCmdline() {
    return api.get('/api/v1/cmdline')
  },
  
  // Âà∑Êñ∞‰ªªÂä°
  refreshTasks() {
    return api.post('/api/v1/tasks/refresh')
  },
  
  // Âà∑Êñ∞ÈÖçÁΩÆ
  refreshConfig() {
    return api.post('/api/v1/config/refresh')
  },
  
  // DebugÁõ∏ÂÖ≥Êé•Âè£
  // Ëé∑ÂèñHeap‰ø°ÊÅØ
  getHeapInfo() {
    return api.get('debug/pprof/heap?debug=1', {
      responseType: 'text'
    });
  },
  
  // Ëé∑ÂèñGoroutine‰ø°ÊÅØ
  getGoroutineInfo() {
    return api.get('debug/pprof/goroutine?debug=1', {
      responseType: 'text'
    });
  },
  
  // Ëé∑ÂèñMetrics‰ø°ÊÅØ
  getMetricsInfo() {
    return api.get('metrics', {
      responseType: 'text'
    });
  },

  // Ëé∑ÂèñÊó•Âøó
  getLog(from = 0, error = undefined) {
    const params = { from };
    if (error !== undefined) {
      params.error = error;
    }
    return api.get('/api/v1/log', {
      params
    })
  },

  // Ëé∑Âèñ‰ªªÂä°ÈÖçÁΩÆ
  getTaskConfig(taskName) {
    return api.get(`/api/v1/task/${taskName}`)
  },

  // Ëé∑Âèñ‰ªªÂä°dbkey
  getTaskDbKey(taskName) {
    return api.get(`/api/v1/dbkey/${taskName}`)
  }
}