<template>
  <div class="logs-container">
    <div class="card">
      <div class="card-header">
        <h2>Â∫îÁî®Êó•Âøó</h2>
      </div>
      <div class="card-body">
        <div v-if="loading" class="loading-state">
          <div class="loading-spinner"></div>
          <span>Âä†ËΩΩ‰∏≠...</span>
        </div>
        <div v-else-if="error" class="error-state">
          <p>{{ error }}</p>
          <button class="btn retry-btn" @click="fetchLogs">ÈáçËØï</button>
        </div>
        <div v-else>
          <!-- ÊéßÂà∂ÈÄâÈ°π -->
          <div class="logs-controls">
            <div class="control-group">
              <label class="control-item">
                <input type="checkbox" v-model="formatJson">
                <span>Ê†ºÂºèÂåñJSON</span>
              </label>
              <label class="control-item">
                <input type="checkbox" v-model="enableWrap">
                <span>Êç¢Ë°åÊòæÁ§∫</span>
              </label>
              <label class="control-item">
                <input type="checkbox" v-model="showErrorLogs">
                <span>‰ªÖÊòæÁ§∫ÈîôËØØÊó•Âøó</span>
              </label>
            </div>
            <div class="control-actions">
              <button class="btn refresh-btn" @click="refreshLogs" :disabled="loading">
                <span v-if="!loading">Âà∑Êñ∞</span>
                <span v-else>Âà∑Êñ∞‰∏≠...</span>
              </button>
            </div>
          </div>
          
          <div class="logs-content">
            <div v-if="logs.length === 0" class="empty-state">
              ÊöÇÊó†Êó•ÂøóÊï∞ÊçÆ
            </div>
            <div v-else class="code-container">
              <div class="line-numbers">
                <div v-for="i in logs.length" :key="i" class="line-number">{{ i }}</div>
              </div>
              <pre class="logs-pre" :class="{ 'nowrap': !enableWrap }" v-html="formatLogs(logs)"></pre>
            </div>
          </div>
          
          <!-- ÂàÜÈ°µÊéß‰ª∂ -->
          <div class="pagination">
            <button 
              class="page-btn" 
              :disabled="currentPage === 1"
              @click="goToPage(1)"
            >
              È¶ñÈ°µ
            </button>
            <button 
              class="page-btn" 
              :disabled="currentPage === 1"
              @click="goToPage(currentPage - 1)"
            >
              ‰∏ä‰∏ÄÈ°µ
            </button>
            <span class="page-info">
              Á¨¨ {{ currentPage }} / {{ totalPages }} È°µÔºåÂÖ± {{ total }} Êù°ËÆ∞ÂΩï
            </span>
            <button 
              class="page-btn" 
              :disabled="currentPage === totalPages"
              @click="goToPage(currentPage + 1)"
            >
              ‰∏ã‰∏ÄÈ°µ
            </button>
            <button 
              class="page-btn" 
              :disabled="currentPage === totalPages"
              @click="goToPage(totalPages)"
            >
              Êú´È°µ
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import apiService from '@/services/api.js'

export default {
  name: 'LogsView',
  data() {
    return {
      logs: [],
      total: 0,
      currentPage: 1,
      pageSize: 100,
      loading: false,
      error: null,
        formatJson: false,
        enableWrap: true,
        showErrorLogs: false
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.total / this.pageSize);
    }
  },
  watch: {
    // ÁõëÂê¨‰ªÖÊòæÁ§∫ÈîôËØØÊó•ÂøóÈÄâÈ°πÁöÑÂèòÂåñÔºåËá™Âä®Âà∑Êñ∞Êó•Âøó
    showErrorLogs() {
      // ÂàáÊç¢ÈÄâÈ°πÊó∂ÈáçÁΩÆÂà∞Á¨¨‰∏ÄÈ°µ
      this.currentPage = 1;
      this.fetchLogs();
    }
  },
  mounted() {
    this.fetchLogs();
  },
  methods: {
    async fetchLogs() {
      this.loading = true;
      this.error = null;
      
      const from = (this.currentPage - 1) * this.pageSize;
      
      try {
        // Áõ¥Êé•‰º†ÈÄíÂèÇÊï∞Ôºå‰∏çÂÜçÂµåÂ•óÂú®ÂØπË±°‰∏≠
        const response = await apiService.getLog(from, this.showErrorLogs ? true : undefined);
        console.log('Ëé∑ÂèñÊó•ÂøóÊàêÂäü:', response.data);
        this.logs = response.data.entity.Lines || [];
        this.total = response.data.entity.Total || 0;
      } catch (error) {
        this.error = `Ëé∑ÂèñÊó•ÂøóÂ§±Ë¥•: ${error.response?.data || error.message || 'Êú™Áü•ÈîôËØØ'}`;
        console.error('Êó•ÂøóËé∑ÂèñÈîôËØØ:', error);
      } finally {
        this.loading = false;
      }
    },
    
    formatLogs(logArray) {
      if (!Array.isArray(logArray) || logArray.length === 0) {
        return '';
      }
      
      return logArray.map(log => {
        try {
          // Â∞ùËØïJSONÊ†ºÂºèÂåñ
          if (this.formatJson) {
            // Ê£ÄÊü•ÊòØÂê¶‰∏∫JSONÂ≠óÁ¨¶‰∏≤ÔºàÊõ¥ÂÅ•Â£ÆÁöÑÊ£ÄÊµãÔºâ
            let parsedLog;
            if (typeof log === 'string') {
              // Ê∏ÖÁêÜÂèØËÉΩÁöÑÂâçÂêéÁ©∫ÁôΩÂ≠óÁ¨¶
              const trimmedLog = log.trim();
              // Ê£ÄÊü•ÊòØÂê¶Á¨¶ÂêàJSONÊ†ºÂºè
              if ((trimmedLog.startsWith('{') && trimmedLog.endsWith('}')) ||
                  (trimmedLog.startsWith('[') && trimmedLog.endsWith(']'))) {
                // Â∞ùËØïËß£ÊûêJSON
                parsedLog = JSON.parse(trimmedLog);
                // ËøîÂõûÈ´ò‰∫ÆÊ†ºÂºèÂåñÁöÑJSON
                return this.highlightJson(JSON.stringify(parsedLog, null, 2));
              }
            }
          }
          // ÊôÆÈÄöÊñáÊú¨ÊòæÁ§∫
          return this.escapeHtml(log);
        } catch (e) {
          // JSONËß£ÊûêÂ§±Ë¥•ÔºåËøîÂõûÂéüÊñáÊú¨
          return this.escapeHtml(log);
        }
      }).join('\n');
    },
    
    // ËΩ¨‰πâHTMLÂ≠óÁ¨¶
    escapeHtml(text) {
      const map = {
        '&': '&amp;',
        '<': '&lt;',
        '>': '&gt;',
        '"': '&quot;',
        "'": '&#039;'
      };
      return text.replace(/[&<>"']/g, m => map[m]);
    },
    
    // JSONËØ≠Ê≥ïÈ´ò‰∫Æ
    highlightJson(json) {
      return json
        .replace(/"([^"\\]|\\.)*"/g, '<span class="json-string">$&</span>') // Â≠óÁ¨¶‰∏≤
        .replace(/\b(true|false|null)\b/g, '<span class="json-keyword">$&</span>') // ÂÖ≥ÈîÆÂ≠ó
        .replace(/\b\d+(\.\d+)?\b/g, '<span class="json-number">$&</span>') // Êï∞Â≠ó
        .replace(/([{}[\]])/g, '<span class="json-punctuation">$&</span>') // Ê†áÁÇπÁ¨¶Âè∑
        .replace(/([:,])/g, '<span class="json-separator">$&</span>'); // ÂàÜÈöîÁ¨¶
    },
    
    goToPage(page) {
      if (page >= 1 && page <= this.totalPages && page !== this.currentPage) {
        this.currentPage = page;
        this.fetchLogs();
      }
    },
    
    // Âà∑Êñ∞Êó•ÂøóÔºàÈáçÊñ∞Âä†ËΩΩÂΩìÂâçÈ°µÔºâ
    refreshLogs() {
      this.fetchLogs();
    }
  }
}
</script>

